.Bbutton {
    /* margin: 12px; */
    margin-bottom: 5px;
    margin-left: 10px;
    height: 40px;
    min-width: 40px;
    border-radius: 5px;
    background: #333;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 2px #444, 5px 5px 15px #222, inset 5px 5px 10px #444, inset -5px -5px 10px #222;
    /* font-family: 'Damion', cursive; */
    border: none;
    font-size: 16px;
    color: rgb(255, 255, 255);
    transition: 500ms;
    display: flex;
  }

  .Bbutton a {
    color: #ffffff;
    text-decoration: none;
    margin-left:10px ;
    margin-right: 10px;
  }

  .Abutton{
      /* margin: 12px; */
      margin-bottom: 5px;
      margin-left: 10px;
      height: 40px;
      min-width: 40px;
      border-radius: 5px;
      background: #f9e5ff44;
      justify-content: center;
      align-items: center;
      box-shadow: 0px 0px 2px #f6d6ff, 5px 5px 15px #c300ff44, inset 5px 5px 10px #444, inset -5px -5px 10px #222;
      /* font-family: 'Damion', cursive; */
      border: none;
      font-size: 16px;
      color: rgb(255, 255, 255);
      transition: 500ms;
      display: flex;
  }
  .Bbutton:hover {
    box-shadow: -2px -2px 10px #444, 5px 5px 15px #222, inset 5px 5px 10px #222, inset -5px -5px 10px #444;
    color: #d6d6d6;
    transition: 500ms;
  }
  .Abutton:hover {
    box-shadow: -2px -2px 10px #444, 5px 5px 15px #222, inset 5px 5px 10px #222, inset -5px -5px 10px #444;
    color: #ffffff;
    transition: 500ms;
  }